import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslateConfigService } from 'src/app/services/translate-config.service';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { FundDetailsService } from './fundDetailsService';
import * as HttpStatus from 'http-status-codes';

@Component({
  selector: 'app-fund-details',
  templateUrl: './fund-details.page.html',
  styleUrls: ['./fund-details.page.scss'],
})
export class FundDetailsPage implements OnInit {
  selectedLanguage:string;
  isShowingLoader:any;
  fundData:any;
  fundDetails:any =[];
  errorMsg:any;
  selectedPolicyData :any;
  selectedPlanType :any;
  encryptedPolicyNo:any;
  canTryAgain = false;
  fundName:any;
  constructor(
    private translateConfigService: TranslateConfigService,
    public translate:TranslateService,
    public fundDetailsService: FundDetailsService,
    public logger:LoggerServiceProvider,
    public utils:UtilService, private config: ConfigProvider
  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
      this.translateConfigService.setLanguage('en');

      this.selectedPolicyData =    history.state.selectedPolicy;
      this.selectedPlanType = this.selectedPolicyData['planType'];
      this.encryptedPolicyNo = this.selectedPolicyData['encryptedPolicyNo'];
      this.fundData = history.state.fundData;
      this.fundName = this.fundData.name;
      this.utils.analytics("fundDetails");
      this.config.currentPage.pageName = "FundDetailsPage";
   }

  ngOnInit() {
    this.getFundDetails();
  }
  ionViewDidLoad() {
    this.logger.log('ionViewDidLoad FundDetailsPage');
    this.getFundDetails();
  }

  getFundDetails = function(){
    if(this.utils.isDeviceOnLine()){
      this.canTryAgain = false;
      this.isShowingLoader = true;
      this.errorMsg= "";
      this.fundDetailsService.getFundDetails(this.encryptedPolicyNo,this.fundData.code,this.fundData.rider).then(
        (res) =>{
          try {
            this.isShowingLoader=false;
            if (res['responseJSON'] && res['responseJSON']['statusCode'] == HttpStatus.OK) {
              this.fundDetails = res['responseJSON']['array'];
              if(!(this.fundDetails.length>0)){
                    this.errorMsg = this.translate.instant('generic_msgs.no_data');
              }
            }else{
              this.errorMsg = res['responseJSON']['error_description'];
            }
          }catch (e) {
            this.isShowingLoader=false;
            this.canTryAgain = true;
            this.logger.error("getFundAllocationsList catch eror : " + JSON.stringify(e));
            this.errorMsg = this.translate.instant('generic_msgs.no_data');
          }
        }, (err) => {
        this.isShowingLoader=false;
        this.canTryAgain = true;
        this.errorMsg = this.utils.handleServiceException(err);
        });
      }else{
        this.isShowingLoader = false;
        this.canTryAgain = true;
        this.errorMsg= this.translate.instant('generic_msgs.server_err');
       }
}

/**toggle */
expandItem(item){
  this.fundDetails.map((listItem) => {

     if(item == listItem){
         listItem.expanded = !listItem.expanded;
     } else {
         listItem.expanded = false;
     }

     return listItem;

 });
}

}
